<template>
  <div>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="销售组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="客户名称" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="customerListChange" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li>客户编号</li>
                <li>客户名称</li>
                <li>联系人</li>
                <li>联系电话</li>
                <li>区域</li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="三包订单单据号" size="mini">
            <el-input v-model="params.order_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务员" size="mini">
            <el-select clearable v-model="params.employee_id" @focus="EmployeeListInvok" @change="businessChange" filterable>
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="审核状态" size="mini">
            <el-select v-model="params.auditor_status" clearable>
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>

          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="salesAddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button @click="audit_enable" size="mini" icon="iconfont icon-pass" type="primary" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain @click="AUDIT_GET" size="mini" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="AUDIT_LIST" size="mini" v-if="authority.b_invalid==1">作废</el-button>
        <!-- <el-button plain icon="el-icon-download" size="mini" @click="downLoadFile" v-if="authority.b_export_file==1">下载模板</el-button>
        <el-button plain icon="iconfont icon-daoru5" @click="dialogEmployee" size="mini" v-if="authority.b_import==1">期初导入</el-button> -->
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="100">
            <template slot-scope="scope">
              <el-button type="text" @click="seeDetail(scope, scope.row)" size="small" v-if="authority.b_info==1">
                查看
              </el-button>
              <el-button @click="salesModifyF(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1">
                修改
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="order_sn" width="180" label="三包订单单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_name" width="120" label="业务员">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="order_num" width="120" label="商品数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" width="120" label="客户名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="80" label="审核状态">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" width="140" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="160" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="130" label="业务时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="150" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="order_price" width="120" label="订单金额" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="rebate_price" width="120" label="本单使用返利" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="credit_price" width="120" label="本单使用授信" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="deposits_price" width="100" label="本单使用预收" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="settlement_price" width="120" label="结算金额" align="right">
            <template slot-scope="scope">
              {{scope.row.settlement_price|priceFormatNone}}
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="abstract" min-width="100" label="摘要">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
    <upload :options="options" @receiveUploadMsg="receiveUploadMsg" ref="upload"></upload>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleThreeorderIndex",
  data() {
    return {
      authority: {},
      options: {
        title: "三包订单期初导入",
        url: "/aftersale/Order/importOrder"
      },
      loading: false,
      timeValue1: [],
      TableData: {
        tableData: [],
        total: 10
      },
      timeValue: [],
      departments: {
        options: []
      },
      params: {},
      searchParams: {},
      goPage: 1, //默认搜索页面
      pageSize: 30,
      per_page: [30, 50, 100, 500],
      multipleSelection: [] //已选项
    };
  },
  computed: {
    ...mapGetters(["threeorderIndex_Get"])
  },
  methods: {
    ...mapActions(["threeorderIndexACT", "threeorderAdd", "examine"]),
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "employee_id", ""); // 初始化业务员下拉选中值
    },
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    receiveUploadMsg(response) {
      getData(this.searchParams);
    },
    businessChange(val) {
      if (val) {
        this.params.employee_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    customerListChange(val) {
      if (val) {
        this.params.customer_name = this.customerList.filter(
          obj => obj.customer_id === val
        )[0].customer_name;
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    downLoadFile() {
      // 下载模板
      this.$axMethod.orderImportClaim().then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    audit_enable(api) {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].order_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auidtorPass_Order({
          order_id: rebateArr
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            getData(this.searchParams); //获取销售订单列表
          }
        });
      //审核
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].order_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auditorNoPass_Order({
          order_id: rebateArr
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            getData(this.searchParams); //获取销售订单列表
          }
        });
      //弃审
    },
    AUDIT_LIST() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].order_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .auditorInvalid_Order({
          order_ids: rebateArr
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg,
              type: "success"
            });
            getData(this.searchParams); //获取销售订单列表
          }
        });
    },
    seeDetail(scope, row) {
      this.examine({
        // moduleName: "examine",
        order_id: row.order_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/threeorder/three_examine"
      });
    },
    salesModifyF(scope, row) {
      this.threeorderAdd({
        // moduleName: "threeorderAdd",
        order_id: row.order_id,
        amend: 1,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/threeorder/threeorder_Add"
      });
    },
    care() {
      //三包照顾
      this.multipleSelection;
    },
    salesAddF() {
      this.threeorderAdd({
        // moduleName: "threeorderAdd",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/threeorder/threeorder_Add"
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.timeValue1 = [];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getOrderList_Order", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
      // wz cache
      this.threeorderIndexACT({
        params: {},
        goPage: this.goPage,
        pageSize: this.pageSize
        // commont: "getTireListIndex"  // 该模块不需要 因使用keep-alive
      });
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage
      });
      getData(this.searchParams);
      // wz cache
      this.threeorderIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
        // commont: "getTireListIndex"  // 该模块不需要 因使用keep-alive
      });
    },
    //删除
    deletes() {
      var deledata = {
        deleID: "order_id",
        deleSelect: this.multipleSelection,
        deleApi: "delOrderSB",
        list: {
          UrlApi: "getOrderList_Order",
          data: this.TableData,
          esData: Object.assign({}, this.params)
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    companyChange(val) {
      // getDepartmentListCommon(this, this.params, 1);
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }

      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "customer_id", ""); // 初始化客户名称下拉选中值
      this.$set(this.params, "employee_id", ""); // 初始化业务员下拉选中值
    }
  },
  created() {
    // wz cache
    let wz_search_params = {};
    this.authority = JSON.parse(localStorage.getItem("authority"));
    if (this.threeorderIndex_Get && this.threeorderIndex_Get.params) {
      if (this.threeorderIndex_Get.params.start_time) {
        const { start_time, end_time } = this.threeorderIndex_Get.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      if (this.threeorderIndex_Get.params.auditor_start_time) {
        const {
          auditor_start_time,
          auditor_end_time
        } = this.threeorderIndex_Get.params;
        this.timeValue1 = this.timeValue1.concat([
          auditor_start_time,
          auditor_end_time
        ]);
      }
      this.$set(this, "params", this.threeorderIndex_Get.params);
      wz_search_params = this.params;
      opthionList(this, this.threeorderIndex_Get.params);
    }
    if (this.threeorderIndex_Get && this.threeorderIndex_Get.goPage) {
      this.params.page = this.threeorderIndex_Get.goPage;
      this.params.per_page = this.threeorderIndex_Get.pageSize;
    }
    this.$set(this, "searchParams", {
      params: wz_search_params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOrderList_Order", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
  }
};
</script>
